/* Navbar.css */
.navbar {
  background-color: #f0f0f0; /* Subtle gray background color */
  color: #333; /* Text color */
  padding: 1px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  justify-content: space-between; /* Pushes the logo to the left and the links to the right */
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  width: 60px;
  height: 60px;
  image-rendering: crisp-edges; /* or pixelated, auto, etc. */
}
.nav-links {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-link {
  margin-right: 20px;
}

.nav-link a {
  text-decoration: none;
  color: #333; /* Text color for links */
  font-weight: bold;
}
