.ModalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999; /* Set a high z-index value to ensure it appears above other elements */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .ModalContent {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    max-width: 300px;
    margin: 0 auto;
    text-align: center;
    z-index: 1000; /* Set a higher z-index value to ensure it appears above other elements */
  }
  
  .ModalHeader {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .ModalInput {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  .ModalButton {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .ModalButton:hover {
    background-color: #45a049;
  }
  
  .ModalTextarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
    box-sizing: border-box;
    min-height: 100px;
  }
  