/* KompanijaDetails.css */

.KompanijaDetailsModalContent {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    max-width: 500px; /* Increase the max-width to make the modal bigger */
    margin: 0 auto;
    text-align: center;
    position: relative; /* Add position relative for centering */
    top: 50%; /* Move the modal down by 50% of the parent's height */
    transform: translateY(-50%); /* Center the modal vertically */
  }
  
  .KompanijaDetailsModalHeader {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .KompanijaDetailsModalTextarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
    box-sizing: border-box;
    min-height: 200px; /* Increase the min-height for a taller textarea */
  }
  
  .KompanijaDetailsModalButton {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    margin-right: 5px;
  }
  
  .KompanijaDetailsModalButton.Cancel {
    background-color: #ff0000;
  }
  
  .KompanijaDetailsModalButton.Cancel:hover {
    background-color: #c62828;
  }
  
  /* Hover effect for both buttons */
  .KompanijaDetailsModalButton:hover,
  .KompanijaDetailsModalButton.Cancel:hover {
    background-color: #45a049;
  }
  