/* KompanijePage.css */
.kompanije-naslov {
  display: flex;
  justify-content: center; /* Horizontal centering */
}

.kompanije-container {
  padding-top: 50px; /* Add some padding-top to create space between the navbar and the table */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

table {
  border-collapse: collapse;
  width: 80%; /* Reduce the width by 20% */
  max-width: 1600px; /* Adjust max-width as needed */
  margin-bottom: 50px; /* Add a margin to separate the table from the footer */
}

th, td {
  border: 1px solid #000;
  padding: 5px; /* Slightly reduce the padding */
  text-align: center;
  font-size: 12px; /* Reduce the font size by 20% */
  line-height: 20px; /* Reduce the row height by 33% */
}

th {
  background-color: #ccc; /* Use a slightly darker grey color for table header */
}

tr:nth-child(even) {
  background-color: #dcdcdc; /* Use a slightly darker grey color for even rows */
}

tr:nth-child(odd) {
  background-color: #fff; /* Keep odd rows white */
}

.file-input-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%; /* Add this line to occupy the full width */
  margin-top: 10px; /* Add some top margin for spacing */
}

.file-input-label {
  margin: 0;
  padding: 8px;
  border: 1px solid #000;
  border-radius: 4px;
  background-color: #4CAF50; /* Green background color */
  color: white;
  cursor: pointer;
  font-size: 14px;
}

.file-input-label:hover {
  background-color: #45a049; /* Darker green on hover */
}
/* Add these styles to your KompanijePage.css file */
.search-input {
  border-radius: 10px; /* Adjust the value as needed to control the roundness */
  padding: 8px;
  font-size: 12px;
  margin-bottom: 10px;
}

/* Add these styles to your KompanijePage.css file */
.green-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 10px; /* Adjust the border-radius as needed for rounded corners */
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.green-button:hover {
  background-color: #45a049;
}

.details-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
}

.details-button:hover {
  background-color: #45a049;
}