.ModalContent {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    max-width: 300px;
    margin: 0 auto;
    text-align: center;
}

.ModalHeader {
    font-size: 24px;
    margin-bottom: 20px;
}

.ModalInput {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box; /* Add this to prevent padding and border from increasing the width */
}

.ModalButton {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
}

.ModalButton:hover {
    background-color: #45a049;
}

/* Add this style to your KontaktModal.css file */
.ModalTextarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
    box-sizing: border-box;
    min-height: 100px; /* Adjust the min-height as needed for the textarea */
}


