/* HomePage.css */
.home-container {
    max-width: 800px;
    margin: 80px auto;
    padding: 20px;
    text-align: justify;
    font-family: 'Arial', sans-serif; /* Change the font */
  }
  
  .home-header h1 {
    font-size: 36px;
    color: #333;
    text-align: center; /* Center align the heading */
    margin-bottom: 20px; /* Add some space below the heading */
  }
  
  .home-content {
    font-size: 18px;
    line-height: 1.6;
    color: #666;
  }
  
  .home-content p {
    margin-bottom: 15px;
  }
