


.kontakti-naslov {
    display: flex;
    justify-content: center; /* Horizontal centering */
  }
  
  .kontakti-container{
    padding-top: 50px; /* Add some padding-top to create space between the navbar and the table */
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  table {
    width: 90%; /* Make the table 90% wide of its container */
    max-width: 1000px; /* Limit the maximum width of the table */
    margin: 0 auto; /* Center the table horizontally */
  }

  .add-contact-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px; /* Adjust the value for roundness */
    padding: 10px 20px; /* Adjust the padding for size */
    font-size: 16px; /* Adjust the font size if needed */
    cursor: pointer;
  }
  
  .add-contact-button:hover {
    background-color: #45a049; /* Darker green on hover */
  }